.name {
  position: relative;
}
.text-curve {
  font-size: 70px;
  text-anchor: middle;
  fill: #fff;
}

#curve {
  fill: transparent;
}

.ring-section {
  position: absolute;

  &__top {
    top:0;
    left: 5px;
    width: 190px;
    height: 95px;
  }
  &__left {
    top:104px;
    left: 101px;
    width: 95px;
    height: 95px;
    transform: rotate(180deg);
  }
  &__right {
    top:100px;
    left: 0;
    width: 95px;
    height: 95px;
    transform: rotate(270deg);  
  }
}

.visit-arrow {
  position: absolute;
  top: 135px;
  right: 40px;
}

.magnifier {
  position: absolute;
  top: 135px;
  left: 40px;
}